import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

//Import RxJs required methods
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class service {

    serviceUrl: string;
    // Resolve HTTP using the constructor
    constructor(private http: Http) {
        // this.serviceUrl = "36.250.234.66:8083/RiverChiefSystem";//
        // this.serviceUrl = "36.250.234.66:8080/RiverChiefSystem";//
        //this.serviceUrl = "192.168.43.226:8080/RiverChiefSystem";//
        this.serviceUrl = "36.250.234.65:8081/RiverChiefSystem";//
        //this.serviceUrl = "200.200.200.182:8080/RiverChiefSystem";//
        //this.serviceUrl = "47.104.207.114:8082/RiverChiefSystem";//
    }
    // private instance variable to hold base url
    public service(url: string, p: any): Observable<Comment[]> {
        let headerscontent = 'application/x-www-form-urlencoded';

        if ((typeof p) == 'object') {// Stringify payload 
            p = JSON.stringify(p);
            headerscontent = 'application/json';
        }
        let headers = new Headers({
            'Content-Type': headerscontent // ... Set content type
        });
        let options = new RequestOptions({ headers: headers }); // Create a request option

        return this.http.post(url, p, options) // ...using post request
            .map((res: Response) => res.json())
            .catch((error: any) => Observable.throw(error.json().error || 'Server error'));
    }
}


