    /**
     * 
     */
    private pointerStyle(e) {
        let pixel = this.map.getEventPixel(e.originalEvent);
        let hit = this.map.hasFeatureAtPixel(pixel);
        if (hit) {
            this.map.getTargetElement()['style'].cursor = 'pointer';
        }
        else {
            this.map.getTargetElement()['style'].cursor = '';
        }
    }
    /**
     * ON
     */
    public pointerStyle_on() {
        this.map.on('pointermove', this.pointerStyle);
    }
    /**
     * OFF
     */
    public pointerStyle_off() {
        this.map.un('pointermove', this.pointerStyle);
    }

    /**
     * 
     * @param p 
     * @param p_type  
     */
    public set_landmarks(p: any[], p_type: string) {
        let p_url = "./assets/landmarkStyles/landmark";
        if (p != null) {
            for (let i = 0; i < p.length; i++) {
                let x = document.createElement("img");
                x.setAttribute("src", p_url + p_type + '.png');
                x.setAttribute("data-id", p[i]['id']);
                x.setAttribute("data-code", p_type);
                let flag_css = "cursor_p " + "landmark" + p_type;
                if (p[i]['warn'] == 1) {
                    flag_css = "css_warn " + flag_css;
                }
                x.setAttribute("class", flag_css);

                let flag = new ol.Overlay({
                    element: x
                });
                flag.setPosition([p[i]['x'], p[i]['y']]);
                this.map.addOverlay(flag);
            }
        }
    }

