    public select_on(p_layer?: ol.layer.Vector | ol.layer.Vector[], func?: Function) {
        let viewport = this.map.getViewport();

        let html = $("<button>").attr("id", "map_select").html("").addClass('btn btn-primary btn-sm position_a bottom_10p right_10p');

        $(viewport).children(".ol-overlaycontainer-stopevent").append(html);

        // 
        let flag_on = false;
        $("#map_select").click(() => {
            if (flag_on) { // true
                flag_on = false;
                $("#map_select").html("");
                this.selectInteraction_off();
            } else { // false
                flag_on = true;
                $("#map_select").html("");
                this.selectInteraction();
            }
        });
    }

