import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
// import { Geolocation } from '@ionic-native/geolocation';

//
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";
import { UtilsService } from "./../../service/utils";

import { LoginPage } from "./../login/login";

// declare var cordova: any;

@Component({
    selector: 'page-messageDetails',
    templateUrl: 'messageDetails.html',
    providers: [StorageService, service, UtilsService]
})
export class MessageDetailsPage {

    user: any;
    userId: any;
    token: any;
    id: any;
    x: any;
    y: any;
    code: any;
    title: any;

    billboard: any;

    //
    pollutant_source_name: any;
    type_name: any;
    access: any;
    focus_level: any;
    region_name: any;
    river_name: any;
    monitor: any;

    //
    WaterIntake_name:any;
    WaterIntake_type:any;
    WaterIntake_administration:any;
    WaterIntake_area:any;
    WaterIntake_region_name:any;
    WaterIntake_river_name:any;
    WaterIntake_x:any;
    WaterIntake_y:any;

    //
    WaterProject_name :any;
    WaterProject_base_info_type:any;
    WaterProject_progress:any;
    WaterProject_investment:any;
    WaterProject_personliable:any;
    WaterProject_telephone:any;
    WaterProject_site:any;
    WaterProject_start_time:any;
    WaterProject_imageurl:any;
    WaterProject_company:any;
    WaterProject_content_and_scale:any;
    WaterProject_problems:any;

    //
    Outfall_name:any;
    Outfall_riverchief:any;
    Outfall_area:any;
    Outfall_type:any;
    Outfall_intoriver_type:any;
    Outfall_longitute:any;
    Outfall_latitude:any;
    Outfall_resource:any;
    Outfall_target:any;
    Outfall_imgurl:any;
    Outfall_identification:any;
    Outfall_remark:any;
    Outfall_region_name:any;
    Outfall_river_name:any;
    Outfall_type_name:any;

    type: any;

    constructor(public navCtrl: NavController,
        private utils: UtilsService,
        private StorageService: StorageService,
        private service: service,
        public params: NavParams,
        // private toast: Toast,
        ) {

        this.user = this.StorageService.read("user");
        this.userId = this.user['userId'];
        this.token = this.user['token'];
        this.id = this.params.get("id");
        this.type = this.params.get("type");
        this.code = this.params.get("code");
        if (this.type == 4) {
            this.title = "";
        }else if (this.type == 5) {
            this.title = "";
        }else if (this.type == 6) {
            this.title = "";
        } else if (this.type == 7) {
            this.title = "";
        }
    }

    ngOnInit() {
        let p = "userId=" + this.userId
            + "&token=" + this.token
            + "&code=" + this.code
            + "&id=" + this.id;
        let url = 'http://' + this.service.serviceUrl + '/detailInformationController/getDetailInformation.do';

        console.log(p);

        this.service.service(url, p).subscribe(
            data => {
                if (data['result'] == 1) {
                    if (this.type == 6) {
                        this.billboard = data['PollutantSource'];
                        this.pollutant_source_name = this.billboard['pollutant_source_name'];
                        this.type_name = this.billboard['type_name'];
                        this.access = this.billboard['access'];
                        this.focus_level = this.billboard['focus_level'];
                        this.region_name = this.billboard['region_name'];
                        this.river_name = this.billboard['river_name'];
                        this.monitor = this.billboard['monitor'];
                    }else if(this.type == 4){
                        this.billboard = data['WaterIntake'];
                        this.WaterIntake_name = this.billboard['name'];
                        this.WaterIntake_type = this.billboard['type'];
                        this.WaterIntake_administration = this.billboard['administration'];
                        this.WaterIntake_area = this.billboard['area'];
                        this.WaterIntake_region_name = this.billboard['region_name'];
                        this.WaterIntake_river_name = this.billboard['river_name'];
                        this.WaterIntake_x = this.billboard['longitute'];
                        this.WaterIntake_y = this.billboard['latitude'];
                    }else if(this.type == 7){
                        this.billboard = data['WaterProject'];
                        this.WaterProject_name = this.billboard['name'];
                        this.WaterProject_base_info_type = this.billboard['base_info_type'];
                        this.WaterProject_progress = this.billboard['progress'];
                        this.WaterProject_investment = this.billboard['investment'];
                        this.WaterProject_personliable = this.billboard['personliable'];
                        this.WaterProject_telephone = this.billboard['telephone'];
                        this.WaterProject_site = this.billboard['site'];
                        this.WaterProject_start_time = this.billboard['start_time_string'];
                        this.WaterProject_imageurl = this.billboard['imageurl'];
                        this.WaterProject_company = this.billboard['company'];
                        this.WaterProject_content_and_scale = this.billboard['content_and_scale'];
                        this.WaterProject_problems = this.billboard['problems'];
                    }else if(this.type == 5){
                        this.billboard = data['Outfall'];
                        this.Outfall_name = this.billboard['outfall_name'];
                        this.Outfall_riverchief = this.billboard['riverchief'];
                        this.Outfall_area = this.billboard['area'];
                        this.Outfall_type = this.billboard['outfall_type'];
                        this.Outfall_intoriver_type = this.billboard['intoriver_type'];
                        this.Outfall_longitute = this.billboard['longitute'];
                        this.Outfall_latitude = this.billboard['latitude'];
                        this.Outfall_resource = this.billboard['resource'];
                        this.Outfall_target = this.billboard['target'];
                        this.Outfall_imgurl = this.billboard['imgurl'];
                        this.Outfall_identification = this.billboard['identification'];
                        this.Outfall_remark = this.billboard['remark'];
                        this.Outfall_region_name = this.billboard['region_name'];
                        this.Outfall_river_name = this.billboard['river_name'];
                        this.Outfall_type_name = this.billboard['type_name'];

                    }


                    console.log("success!");
                    console.log("billboard:", this.billboard);
                } else {
                    alert(",");
                    this.navCtrl.push(LoginPage);
                }

            },
            err => {
                console.error(err);
            }
        );
    }

}



