import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';

import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";
import { OlMapService } from "./../../service/ol_map";

// import { Camera, CameraOptions } from '@ionic-native/camera';
//import { NativeStorage } from '@ionic-native/native-storage';
//import { BackgroundMode } from '@ionic-native/background-mode';
// import { Toast } from '@ionic-native/toast';
// import { Geolocation } from '@ionic-native/geolocation';
import { ToasT } from '../../toast-3/toast-3';

import { HomePage } from '../home/home';

@Component({
    selector: 'page-newEvent',
    templateUrl: 'newEvent.html',
    providers: [service, StorageService, ToasT]
})
export class NewEventPage {
    private userInfo;

    //
    private rPD_csrc: string = "";
    private rPD_csrc_arr: string[] = [];
    private rPD_imgCreatTime: any[] = [];
    private eventLevel = 1;
    private problemType_chkParent = { code: '0' }; //
    private problemType = [];
    private problemTypeALL;
    private eventContent = ""; //
    private btn_do_newEventByImg_disabled = false;
    private btn_do_newEventByImg = "";
    private remark: string = "";

    //
    private rPD_is_reporting: boolean = false;
    //
    private rPD_is_btnStartRivering: boolean = true;

    constructor(
        private navCtrl: NavController,
        private alertCtrl: AlertController,
        private service: service,
        private StorageService: StorageService,
        // private camera: Camera,
        //private nativeStorage: NativeStorage,
        //private backgroundMode: BackgroundMode,
        private toast: ToasT,
    ) {
        this.userInfo = this.StorageService.read('user');

        //this.backgroundMode.enable();
        //this.backgroundMode.disableWebViewOptimizations();
    }

    //
    private rPD_useCamera() {
        // let options: CameraOptions = {
        //     quality: 50,
        //     destinationType: this.camera.DestinationType.DATA_URL, // 
        //     encodingType: this.camera.EncodingType.JPEG, // 
        //     mediaType: this.camera.MediaType.PICTURE, // 
        //     saveToPhotoAlbum: true,
        //     sourceType: this.camera.PictureSourceType.CAMERA // 
        // }

        // this.camera.getPicture(options).then(
        //     imageData => {
        //         let base64Image = 'data:image/jpeg;base64,' + imageData;
        //         this.rPD_csrc_arr.push(base64Image);
        //         this.rPD_imgCreatTime.push(new Date());
        //     },
        //     err => {
        //         // Handle error
        //         this.toast.showShortBottom("").subscribe();
        //     }
        // );
    }

    //
    private rPD_do_queryProblemType_count = 0;
    private rPD_do_queryProblemType() {
        let url = 'http://' + this.service.serviceUrl + '/dateInfoController/queryProblemType.do'
        let url_p = "userId=" + this.userInfo['userId']
            + "&token=" + this.userInfo['token'];

        this.service.service(url, url_p).subscribe(
            data => {
                let dictList = [];
                console.log(data);
                if (data['result'] == -1) {
                    this.toast.toast2("");
                    this.navCtrl.popToRoot();
                } else {
                    dictList = data['dictList'];
                    this.problemTypeALL = dictList;
                }
            },
            err => {
                console.error("ERR", err);
                this.rPD_do_queryProblemType_count++;
                if (this.rPD_do_queryProblemType_count > 10) {
                    this.toast.toast2("");
                    this.navCtrl.popToRoot();
                } else {
                    this.rPD_do_queryProblemType();
                }
            }
        );
    }

    //
    private rPD_do_newEventByImg() {
        if (this.rPD_csrc == null || this.rPD_csrc == "") {
            alert("");
        } else if (this.problemType.length == 0) {
            alert("");
        } else if (this.eventContent == null) {
            alert("");
        } else {
            let url_p = "userId=" + this.userInfo['userId']
                + "&token=" + this.userInfo['token']
                + "&problemType=" + this.problemType
                + "&eventLevel=" + this.eventLevel
                + "&reportType=" + "5"
                + "&regionId=" + this.userInfo['regionId']
                + "&riverId=" + "35010100000000"
                + "&base64Image=" + this.rPD_csrc
                + "&eventContent=" + this.eventContent
                + "&recordPoint=" + ''
                + "&remark=" + this.remark
                + "&sanType=" + this.userInfo["roleId"];

            let url = 'http://' + this.service.serviceUrl + '/eventController/newEventByImg.do';
            this.btn_do_newEventByImg = "";
            this.btn_do_newEventByImg_disabled = true;

            this.service.service(url, url_p).subscribe(
                data => {

                    //let d = data;
                    console.log("", data);
                    //alert(data);
                    this.toast.toast2("");
                    this.btn_do_newEventByImg = ""
                    this.btn_do_newEventByImg_disabled = false;
                    this.navCtrl.pop();
                },
                err => {
                    console.error(err);
                    this.btn_do_newEventByImg = ""
                    this.btn_do_newEventByImg_disabled = false;
                }
            );
        }
    }
    private problemPush(p) {
        for (let i = 0; i < this.problemType.length; i++) {
            if (p == this.problemType[i]) {
                this.problemType.splice(i, 1);
                return;
            }
        }
        this.problemType.push(p);
    }
    private problemColor(p) {
        for (let i = 0; i < this.problemType.length; i++) {
            if (p == this.problemType[i])
                return 'primary';
        }
        return 'white';
    }

    ngAfterViewInit() {
        this.rPD_do_queryProblemType();
    }
}



