import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';

//
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";


import { EventDetailsPage } from './../pandect/eventDetails';
import { RiverTrajectoryPage } from './riverTrajectory';

@Component({
    selector: 'page-log',
    templateUrl: 'log.html',
    providers: [StorageService, service]
})
export class LogPage {

    user: any;
    userId: any;
    token: any;
    type: any;
    name: any;
    patrolRecordId: any;
    riverName: any;
    startTime: any;
    endTime: any;
    patrolRange: any;

    patrolRecord: any[] = [];//
    eventList: any[] = [];//


    constructor(public navCtrl: NavController,
        private StorageService: StorageService,
        private service: service,
        public params: NavParams,
        // private toast: Toast, 
        ) {

        this.user = this.StorageService.read("user");
        this.userId = this.user['userId'];
        this.token = this.user['token'];
        this.patrolRecordId = this.params.get('patrolRecordId');

    }
    private eventDetails(x: number, y: number, z: number, a: number, ) {
        this.navCtrl.push(EventDetailsPage, {
            eventId: x,
            type: y,
        });
    }
    private riverTrajectory(x:number){
		this.navCtrl.push(RiverTrajectoryPage,{
			patrolRecordId:x,
		});
    }

    ngOnInit() {
        let p1 = "userId=" + this.userId
            + "&token=" + this.token + "&patrolRecordId=" + this.patrolRecordId
            ;
        let url = 'http://' + this.service.serviceUrl + '/patrolRecordController/queryPointFromPatrolRecord.do';

        this.service.service(url, p1).subscribe(
            data => {
                this.patrolRecord = data['PatrolRecord'];
                this.eventList = data['PatrolRecord'][0]['eventList'];
                // this.name = this.eventList[0]['userName'];
                // this.startTime = this.eventList[0]['startTime'];
                // this.endTime = this.eventList[0]['endTime'];
                // this.rPD_map.c(this.point);
                console.log("patrolRecord", this.patrolRecord);
                console.log("eventList", this.eventList);
                console.log("success!");
            },
            err => {
                console.error(err);
            }
        );

    }


}



