import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
import { ToasT } from '../../toast-3/toast-3';

//
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";

import { PersonalPage } from "./personal";
import { LoginPage } from "./../login/login";

@Component({
	selector: 'page-changePassword',
	templateUrl: 'changePassword.html',
	providers: [StorageService,service,ToasT]
})
export class ChangePasswordPage {

    user:any;
	userId:any;
    token:any;
    oldPass:any;
    newPass1:any;
    newPass2:any;
    
    result:any;
    constructor(public navCtrl: NavController,
                private StorageService: StorageService,
                private service:service,
                private toast: ToasT) {
            this.user=this.StorageService.read("user");
			this.userId=this.user['userId'];
			this.token=this.user['token'];
    }

    private save(){
        if(this.oldPass==null){
            alert("");
        }
        else if(this.newPass1==null){
            alert("");
        }
        else if(this.newPass2==null){
            alert("");
        }
        else if(this.newPass1!=this.newPass2){
            alert("");
        }
        else{
            let p = "userId="+this.userId
            +"&token="+this.token
            +"&password="+this.oldPass
            +"&newPassword="+this.newPass1;
            let url = 'http://'+this.service.serviceUrl+'/loginController/updateUser.do';
    
            this.service.service(url,p).subscribe(
                data => {
                    this.result = data;
                    if(data['result']!=1){
                        console.log("",data['message']);
                        this.toast.toast2(data['message']);
                    }else{
                        console.log("!");
                        console.log("result:",this.result);
                        this.toast.toast2("");
                        this.navCtrl.popToRoot();
                    }

                },
                err => {
                    console.error(err);
                }
            );
        }
    }



}



