import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';
import { ToasT } from '../../toast-3/toast-3';
import { ChangeDetectorRef } from '@angular/core';

//
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";
//import { NativeStorage } from '@ionic-native/native-storage';

//
import { EventDetailsPage } from './../pandect/eventDetails';
import { RiverTrajectoryPage } from './riverTrajectory';
import { LogPage } from './log';
import { PandectPage } from './../pandect/pandect';
// import { DetailsPage } from "./details";
// import { NewEventPage } from './newEvent';



@Component({
    selector: 'page-riverLog',
    templateUrl: 'riverLog.html',
    providers: [StorageService, service]
})
export class RiverLogPage {

    user: any;
    userId: any;
    token: any;
    reportType: number;
    type: number;
    et: any;
    ee: any;
    ri: any;
    appType: any;
    name: any;
    userName: any = "";
    startTime: any;
    endTime: any;
    eventList: any[] = [];//
    eventList1: any[] = [];
    eventList2: any[] = [];
    eventList3: any[] = [];

    pet: string = "all";//
    page: any = 1;

    constructor(public navCtrl: NavController,
        private StorageService: StorageService,
        private service: service,
        //private nativeStorage: NativeStorage,
        private toast: ToasT,
        public cd: ChangeDetectorRef) {
        this.user = this.StorageService.read("user");
        this.userId = this.user['userId'];
        this.token = this.user['token'];
        if (this.user['roleId'] == 4) {
            this.appType = "2";
        } else {
            this.appType = "1";
        }
    }

    private eventDetails(x: number, y: number, z: number, a: number, ) {
        this.navCtrl.push(EventDetailsPage, {
            eventId: x,
            type: y,
            startTime: z,
            endTime: a,
        });
    }
    private riverTrajectory(x: number) {
        this.navCtrl.push(RiverTrajectoryPage, {
            patrolRecordId: x,
        });
    }
    private log(x: number, y: any) {
        this.navCtrl.push(LogPage, {
            patrolRecordId: x,
            riverName: y,
        });
    }
    private pendect(x: number) {
        this.navCtrl.push(PandectPage, {
            patrolRecordId: x,
        });
    }

    //
    doInfinite(infiniteScroll) {
        console.log('');

        setTimeout(() => {
            this.page += 1;
            this.change(this.page);

            console.log('');
            infiniteScroll.complete();
        }, 1000);
    }
    //
    private change(a: any) { //b  a 
        // console.log("",b);
        // console.log("",a);
        //
        // this.nativeStorage.getItem('user').then(
        //     data => {
        //         let p = "userId=" + this.user['userId']
        //             + "&token=" + this.user['token']
        //             + "&startTime=" + this.startTime
        //             + "&endTime=" + this.endTime
        //             + "&appType=" + this.appType
        //             + "&userName=" + this.userName
        //             + "&pageSize=5"
        //             + "&pageNo=" + a
        //             ;
        //         let url = 'http://' + this.service.serviceUrl + '/patrolRecordController/queryPatrolRecord.do';

        //         this.service.service(url, p).subscribe(
        //             data => {
        //                 if (data['result'] == -1) {
        //                     this.toast.showShortBottom("").subscribe();
        //                     this.navCtrl.popToRoot();
        //                 } else if (data['result'] == 1) {
        //                     if (a == 1) {//
        //                         this.page = 1;
        //                         this.eventList2 = [];
        //                         console.log("", this.eventList2);
        //                         for (let i = 0; i < data['PatrolRecord'].length; i++) {
        //                             this.eventList2.push(data['PatrolRecord'][i]);
        //                         }
        //                         this.cd.detectChanges();
        //                     }else{
        //                         for (let i = 0; i < data['PatrolRecord'].length; i++) {
        //                             this.eventList2.push(data['PatrolRecord'][i]);
        //                         }
        //                     }

        //                     console.log("success!");
        //                     console.log(":", this.eventList2);
        //                     // console.log("",this.eventList2[0]['riverName'].length);
        //                 }

        //             },
        //             err => {
        //                 console.error(err);
        //             }
        //         );

        //     }
        // );
        //
        let data = this.StorageService.read('user');
        if (data != null) {
            let p = "userId=" + this.user['userId']
                + "&token=" + this.user['token']
                + "&startTime=" + this.startTime
                + "&endTime=" + this.endTime
                + "&appType=" + this.appType
                + "&userName=" + this.userName
                + "&pageSize=5"
                + "&pageNo=" + a
                ;
            let url = 'http://' + this.service.serviceUrl + '/patrolRecordController/queryPatrolRecord.do';

            this.service.service(url, p).subscribe(
                data => {
                    if (data['result'] == -1) {
                        this.toast.toast2("");
                        this.navCtrl.popToRoot();
                    } else if (data['result'] == 1) {
                        if (a == 1) {//
                            this.page = 1;
                            this.eventList2 = [];
                            console.log("", this.eventList2);
                            for (let i = 0; i < data['PatrolRecord'].length; i++) {
                                this.eventList2.push(data['PatrolRecord'][i]);
                            }
                            this.cd.detectChanges();
                        } else {
                            for (let i = 0; i < data['PatrolRecord'].length; i++) {
                                this.eventList2.push(data['PatrolRecord'][i]);
                            }
                        }

                        console.log("success!");
                        console.log(":", this.eventList2);
                        // console.log("",this.eventList2[0]['riverName'].length);
                    }

                },
                err => {
                    console.error(err);
                }
            );
        }
    }
    ngOnInit() {
        this.change(1);
    }

}



