import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
// import { Toast } from '@ionic-native/toast';

//
import { service } from "./../../service/service";
import { StorageService } from "./../../service/StorageService";
import { NavParams } from 'ionic-angular/navigation/nav-params'; 

declare var echarts: any;

@Component({
    selector: 'page-statistics',
    templateUrl: 'statistics.html',
    providers: [StorageService, service]
})
export class StatisticsPage {

    user: any;
    flag: boolean;
    userId: any;
    token: any;
    reportType: number;
    type: number;
    code: number;
    regionId: number;
    list1: any;
    river: any;
    private f_data1: any;      // 
    private f_data2: any;      // 


    a: boolean = true;
    b: boolean = false;
    c: boolean = false;
    d: boolean = false;
    eventList: any[] = [];//
    todo: any[] = [];//
    toSolve: any[] = [];//
    solve: any[] = [];//
    problemType: any[] = [{}];//

    pet: string = "DailyTasks";//

    constructor(public navCtrl: NavController,
        private StorageService: StorageService,
        private service: service,
        private params: NavParams,
        // private toast: Toast
        ) {

        this.user = this.StorageService.read("user");
        this.flag = true;
        this.userId = this.user["userId"];
        this.token = this.user["token"];
        this.code = 11;
        this.regionId = this.user["regionId"];
    }

    change(x: any) {
        if (x == 1) {
            this.a = true;
            this.b = this.c = this.d = false;
        } else if (x == 2) {
            this.b = true;
            this.a = this.c = this.d = false;
        } else if (x == 3) {
            this.c = true;
            this.a = this.b = this.d = false;
        } else {
            this.d = true;
            this.a = this.c = this.b = false;
        }
    }

    ngOnInit() {

    }

    ionViewDidEnter() {
        //
        let p1 = "userId=" + this.userId
            + "&token=" + this.token
            + "&regionId=" + this.regionId;
        let url1 = 'http://' + this.service.serviceUrl + '/eventController/countEvent.do';

        //
        let p2 = "userId=" + this.userId
            + "&token=" + this.token;
        let url2 = 'http://' + this.service.serviceUrl + '/statisticalEventController/statisticsProblemTypes.do';

        this.service.service(url1, p1).subscribe(
            data => {
                if (data['result'] == 1) {
                    this.todo = data['todo'];
                    this.toSolve = data['toSolve'];
                    this.solve = data['solve'];
                    console.log("success!");
                    console.log("todo:", this.todo);
                    console.log("toSolve:", this.toSolve);
                    console.log("solve:", this.solve);
                } else {
                    console.error("");
                }
            },
            err => {
                console.error(err);
            }
        );

        this.service.service(url2, p2).subscribe(
            data => {
                if (data['result'] == 1) {
                    this.problemType = data['problemTypeList'];
                    console.log("success!");
                    console.log("problemType:", this.problemType);
                } else {
                    console.error("");
                }
            },
            err => {
                console.error(err);
            }
        );

        let url_p = "type=" + 0
            + "&value=" + this.regionId
            + "&userId=" + this.userId
            + "&token=" + this.token;
        let url = 'http://' + this.service.serviceUrl + '/statisticsController/getStatisticsInformation.do';

        this.service.service(url, url_p).subscribe(
            data => {
                console.log(data);

                // 
                this.f_data1 = data['items1'];
                let areaWaterChart = echarts.init(document.getElementById('regionalWaterQuality'));
                let areaWaterChart_data = [];

                for (let i = 0; i < this.f_data1.length; i++) {
                    let f_json = {
                        value: this.f_data1[i]['count'],
                        name: this.f_data1[i]['region_name']
                    };
                    areaWaterChart_data.push(f_json);
                }

                let optionChart = {
                    title: {
                        text: '',
                        x: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['', '', '',
                            '', '', '',
                            '', '', '',
                            '', '']
                    },
                    series: [
                        {
                            name: '',
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: areaWaterChart_data,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };

                areaWaterChart.setOption(optionChart);
                areaWaterChart.on('click', (p) => {
                    // this.toast.showShortBottom(p.name + '' + p.value + '' + p.percent + '%').subscribe();
                });

                // 
                this.f_data2 = data['items2'];
                if (this.f_data2.length == 0) {
                    alert("");
                }
                else {

                    let waterChart = echarts.init(document.getElementById('waterQuality'));
                    let waterChart_data = [];

                    for (let i = 0; i < this.f_data2.length; i++) {
                        let f_json = {
                            value: this.f_data2[i]['count'],
                            name: this.f_data2[i]['type_name']
                        };
                        waterChart_data.push(f_json);
                    }

                    let optionChart2 = {
                        title: {
                            text: '',
                            x: 'center'
                        },
                        legend: {
                            orient: 'vertical',
                            left: 'left',
                            data: ['', '', '', '', '', '']
                        },
                        series: [
                            {
                                name: '',
                                type: 'pie',
                                radius: '55%',
                                center: ['50%', '60%'],
                                data: waterChart_data,
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                                    }
                                }
                            }
                        ]
                    };

                    // 
                    waterChart.setOption(optionChart2);
                    waterChart.on('click', (p) => {
                        // this.toast.showShortBottom(p.name + '' + p.value + '' + p.percent + '%').subscribe();
                    });
                }
            },

            err => {
                console.error(err);
            }

        );
    }
}



