import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';

import { StorageService } from "./../../service/StorageService";
// import { Toast } from '@ionic-native/toast';
import { ToasT } from '../../toast-3/toast-3';
//import { NativeStorage } from '@ionic-native/native-storage';
import { UtilsService } from './../../service/utils';

import { PandectPage } from './../pandect/pandect';
// import { RecentPlanDetailsPage } from './../patrol/recentPlanDetails';
import { MessagePage } from './../message/message';
import { PersonalPage } from './../personalCenter/personal';
import { DocumentPage } from './../document/document';
import { RiverLogPage } from './../riverLog/riverLog';
import { StatisticsPage } from './../statistics/statistics';
import { LoginPage } from "./../login/login";

import { service } from "./../../service/service";

@Component({
	selector: 'page-home',
	templateUrl: 'home.html',
	providers: [UtilsService, StorageService, service,ToasT],
})
export class HomePage {
	user: any;
	userId: any;
	token: any;
	long: any;
	roleType: any;

	year: any = 0; //
	yearCompleteNum: any = 0; //
	halfyear: any = 0; //
	halfyearCompleteNum: any = 0; //
	quarter: any = 0;//
	quarterCompleteNum: any = 0; //
	month: any = 0;//
	monthCompleteNum: any = 0; //
	twomonth: any = 0;//
	twomonthCompleteNum: any = 0; //

	eventList: any[] = [];//
	adList: any[] = [];//
	list: any[] = [];

	constructor(
		private navCtrl: NavController,
		private StorageService: StorageService,
		private utilsService: UtilsService,
		private alertCtrl: AlertController,
		private toast: ToasT,
		//private nativeStorage: NativeStorage,

		private service: service,
	) {
		this.user = this.StorageService.read("user");
		// this.user=this.nativeStorage.getItem('user');
		this.long = this.StorageService.read('long');
		this.roleType = this.user['roleType'];
		this.list = this.StorageService.read("adList");
		console.log("list", this.list);
		// for (var i = 0; i < this.long; i++) {
			// this.adList[i] = this.StorageService.read("ad" + i);
			// console.log("this.adList[i]", this.adList[i]);
		// }
		// this.userId=this.user['userId'];
		// this.token=this.user['token'];
	}

	private pandect() {
		this.navCtrl.push(PandectPage);
	}
	// private patrol() {
	// 	this.navCtrl.push(RecentPlanDetailsPage);
	// }
	private personal() {
		this.navCtrl.push(PersonalPage);
	}
	private message() {
		this.navCtrl.push(MessagePage);
	}
	private document() {
		this.navCtrl.push(DocumentPage);
	}
	private riverLog() {
		this.navCtrl.push(RiverLogPage);
	}
	private statistics() {
		this.navCtrl.push(StatisticsPage);
	}

	ngOnInit() {
		//
		// this.nativeStorage.getItem('user').then(
		// 	data => {
		// 		this.user = data['user'];
		// 		// console.log("user111:", this.user);
		// 		this.userId = this.user['userId'];
		// 		this.token = this.user['token'];


		// 	}
		// )
		//
		let data = this.StorageService.read('user');
		if (data != null) {
			this.user = data['user'];
			this.userId = data['userId'];
			this.token = data['token'];
		}
	}


	ionViewDidEnter() {


		this.twomonth = this.halfyear = this.year = this.quarter = this.month = 0;
		this.yearCompleteNum = this.halfyearCompleteNum = this.quarterCompleteNum = this.twomonthCompleteNum = this.monthCompleteNum = 0;
		let p = "userId=" + this.userId
			+ "&token=" + this.token;
		let url = 'http://' + this.service.serviceUrl + '/patrolController/countPatrol.do';

		this.service.service(url, p).subscribe(
			data => {
				if (data['result'] == -1) {
                    this.toast.toast2("");
                    this.navCtrl.popToRoot();
				}
				else {
					this.eventList = data['Patrol'];
					// this.StorageService.write('eventList',this.eventList);

					for (var i = 0; i < this.eventList.length; i++) {
						if (this.eventList[i]['patrolPeriod'] == "1") {
							this.year = this.year + this.eventList[i]['patrolNum'] - this.eventList[i]['completeNum'];
							this.yearCompleteNum += this.eventList[i]['completeNum'];
						} else if (this.eventList[i]['patrolPeriod'] == "2") {
							this.halfyear = this.halfyear + this.eventList[i]['patrolNum'] - this.eventList[i]['completeNum'];
							this.halfyearCompleteNum += this.eventList[i]['completeNum'];
						} else if (this.eventList[i]['patrolPeriod'] == "3") {
							this.quarter = this.quarter + this.eventList[i]['patrolNum'] - this.eventList[i]['completeNum'];
							this.quarterCompleteNum += this.eventList[i]['completeNum'];
						} else if (this.eventList[i]['patrolPeriod'] == "4") {
							this.twomonth = this.twomonth + this.eventList[i]['patrolNum'] - this.eventList[i]['completeNum'];
							this.twomonthCompleteNum += this.eventList[i]['completeNum'];
						} else if (this.eventList[i]['patrolPeriod'] == "5") {
							this.month = this.month + this.eventList[i]['patrolNum'] - this.eventList[i]['completeNum'];
							this.monthCompleteNum += this.eventList[i]['completeNum'];
						}
					}
					console.log(":", this.eventList);
					// console.log("--", this.year - this.quarter - this.month);
					console.log("success!");
				}
			},
			err => {
				console.error(err);
			}
		);
	}
}



