import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule, Component, ViewChild } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, Platform ,AlertController, NavController } from 'ionic-angular';
import { HttpModule } from '@angular/http';

import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { PandectPage } from '../pages/pandect/pandect';
// import { RecentPlanDetailsPage } from '../pages/patrol/recentPlanDetails';
import { EventDetailsPage } from '../pages/pandect/eventDetails';
import { EventSolvePage } from '../pages/pandect/eventSolve';
import { MessagePage } from '../pages/message/message';
import { MessageDetailsPage } from '../pages/message/messageDetails';
import { BillboardPage } from '../pages/message/billboard';
import { WaterMonitorPage } from '../pages/message/waterMonitor';
import { Play_moviePage } from '../pages/message/play_movie';
import { PersonalPage } from '../pages/personalCenter/personal';
import { ChangePasswordPage } from '../pages/personalCenter/changePassword';
// import { TestPage } from '../pages/test/test';
import { DocumentPage } from '../pages/document/document';
import { DocumentDetailsPage } from '../pages/document/documentDetails';
import { NewEventPage } from '../pages/pandect/newEvent';
import { RiverLogPage } from '../pages/riverLog/riverLog';
import { RiverTrajectoryPage } from '../pages/riverLog/riverTrajectory';
import { LogPage } from '../pages/riverLog/log';
import { StatisticsPage } from '../pages/statistics/statistics';

@Component({
	template: `<ion-nav #rootNavController [root]="rootPage"></ion-nav>`
})
export class MyApp {
	@ViewChild('rootNavController') navCtrl: NavController;
	rootPage: any = LoginPage;
	// rootPage:any = HomePage;

	private logout_isShow : boolean = false;

	constructor(
		public platform: Platform, 
		public alertCtrl: AlertController,
	) {
		platform.ready().then(() => {
			// Okay, so the platform is ready and our plugins are available.
			// Here you can do any higher level native things you might need.

			//
			platform.registerBackButtonAction(()=>{
				let activeVC = this.navCtrl.getActive();  
				let activePage = activeVC.instance;  
		
				if (activePage instanceof HomePage || activePage instanceof LoginPage) {  
					return this.confirmExitApp();  
				} 
				else {  
					if (!this.navCtrl.canGoBack()) {  
						return this.confirmExitApp();  
					}  
					return this.navCtrl.pop();  
				}
			});
		});
	}

	confirmExitApp() {
		let alert = this.alertCtrl.create({
			title: '',
			subTitle: '',
			buttons: [
				{
					text: '',
					role: 'cancel',
					handler: () => {
						this.logout_isShow = false;

					}
				},
				{
					text: '',
					handler: () => {
						//APP
						//this.backgroundGeolocation.stop();
						this.platform.exitApp();
					}
				}
			]
		});
		if(!this.logout_isShow) { //true
			this.logout_isShow = true;
			alert.present();
		}
	}
}

@NgModule({
	declarations: [
		MyApp,
		HomePage,
		LoginPage,
		PandectPage,
		// RecentPlanDetailsPage,
		EventDetailsPage,
		EventSolvePage,
		MessagePage,
		WaterMonitorPage,
		Play_moviePage,
		PersonalPage,
		ChangePasswordPage,
		DocumentPage,
		// TestPage,
		NewEventPage,
		DocumentDetailsPage,
		RiverLogPage,
		RiverTrajectoryPage,
		StatisticsPage,
		LogPage,
		BillboardPage,
		MessageDetailsPage
	],
	imports: [
		BrowserModule,
		HttpModule,
		IonicModule.forRoot(MyApp)
	],
	bootstrap: [IonicApp],
	entryComponents: [
		MyApp,
		HomePage,
		LoginPage,
		PandectPage,
		// RecentPlanDetailsPage,
		EventDetailsPage,
		EventSolvePage,
		MessagePage,
		WaterMonitorPage,
		Play_moviePage,
		PersonalPage,
		ChangePasswordPage,
		DocumentPage,
		// TestPage,
		NewEventPage,
		DocumentDetailsPage,
		RiverLogPage,
		RiverTrajectoryPage,
		StatisticsPage,
		LogPage,
		BillboardPage,
		MessageDetailsPage
	],
	providers: [
		{ provide: ErrorHandler, useClass: IonicErrorHandler }
	]
})
export class AppModule { }



