    private problemPush(p) {
        for (let i = 0; i < this.problemType.length; i++) {
            if (p == this.problemType[i]) {
                this.problemType.splice(i, 1);
                return;
            }
        }
        this.problemType.push(p);
    }
    private problemColor(p) {
        for (let i = 0; i < this.problemType.length; i++) {
            if (p == this.problemType[i])
                return 'primary';
        }
        return 'white';
    }

//-------- 
    private map : any = null;
    //
    private animating = false;
    private geoMarker : any = null;
    private speed = 20;
    private speedState = 0;
    private now;
    private routeButton : any = null;
    private routeCoords : any = null;
    private geoMarker_Style =  new ol.style.Style({
        image: new ol.style.Circle({
            radius: 7,
            snapToPixel: false,
            fill: new ol.style.Fill({color: 'black'}),
            stroke: new ol.style.Stroke({
                color: 'white', width: 2
            })
        })
    });

    private speedBack(){
        this.speedState = 0;
        this.speed = 20;
    }

    private speedUp(){
        this.speedState = 1;
        this.speed = this.speed * 2;
    }

    private speedDown(){
        this.speedState = 2;
        this.speed = this.speed / 2;
    }

    private PolyLine_Animation_Controller(){
        let lineData : any[] = this.point;
        
        let route = new ol.geom.LineString(lineData); //  LineString
        this.routeCoords = route.getCoordinates(); // 
        //this.routeLength = this.routeCoords.length; // 
        this.routeButton = document.getElementById('AnimationController');
        this.startAnimation();
    }	

    private moveFeature = (event)=> {//eventpostcompose
        let vectorContext = event.vectorContext;
        let frameState = event.frameState;
        if (this.animating) {
            let elapsedTime = frameState.time - this.now;
            let index = Math.round(this.speed * elapsedTime / 1000);
            if (index >= this.routeCoords.length) {
                this.stopAnimation(true);
                return;
            }

            let currentPoint = new ol.geom.Point(this.routeCoords[index]);
            let feature = new ol.Feature(currentPoint);
            vectorContext.drawFeature(feature, this.geoMarker_Style);
        }
        // tell OpenLayers to continue the postcompose animation
        this.map.render();
    };

    private startAnimation() {
        if (this.animating) {
            this.stopAnimation(false);
        } else {
            this.animating = true;
            this.now = new Date().getTime();
            this.routeButton.innerHTML = '';
            // hide geoMarker
            this.geoMarker = this.rPD_map.geoMaker_remove();
            // just in case you pan somewhere else
            let point = 0;
            this.map.getView().setCenter(this.routeCoords[point]);
            this.map.on('postcompose', this.moveFeature);
            this.map.render();
        }
    }

    /**
     *ended boolean,truefalse 
    */
    private stopAnimation(ended) {
        console.log("stopAnimation"+ended);
        this.animating = false;
        this.routeButton.innerHTML = '';

        // if animation cancelled set the marker at the beginning
        let coord = ended ? this.routeCoords[this.routeCoords.length - 1] : this.routeCoords[0];
        /** @type {ol.geom.Point} */ 
        this.geoMarker = this.rPD_map.geoMaker_add();
        (this.geoMarker.getGeometry()).setCoordinates(coord);
        //remove listener
        this.map.un('postcompose', this.moveFeature);
    }
    //
 

    ngOnInit() {
        this.rPD_map = new OlMapService("rPD_map");
        this.rPD_map.startMap();
        let p1 = "userId=" + this.userId
            + "&token=" + this.token + "&patrolRecordId=" + this.patrolRecordId
            ;
        let url = 'http://' + this.service.serviceUrl + '/patrolRecordController/queryPointFromPatrolRecord.do';

        this.service.service(url, p1).subscribe(
            data => {
                this.eventList = data['PatrolRecord'];
                this.point = data['point'];
                this.rPD_lineLength=this.eventList[0]['totalMileage'];
                this.rPD_sumTime=this.eventList[0]['totalTime'];
                // this.rPD_map.c(this.point);
                //this.rPD_map.drawLine(this.point);
                // //
                if((this.point!=null)&&(this.point.length!=0)){
                    this.geoMarker = this.rPD_map.playLine(this.point);
                }
                this.map = this.rPD_map.get_map(); 
                //
                this.speed = 20;
                this.speedState = 0;
                this.rPD_map.setView(this.point[0],14);
                console.log("success!");
                console.log(":", this.eventList);
                console.log("point:", this.point);
            },
            err => {
                console.error(err);
            }
        );
    }
